import { StyleSheet } from "@react-pdf/renderer";

export const styles = StyleSheet.create({
  page: {
    backgroundColor: "#fff",
    color: "#262626",
    fontFamily: "Roboto",
    fontSize: 12,
    padding: 12,
  },
  header: {
    textAlign: "center",
    marginBottom: 4,
  },
  pharmacy: {
    fontSize: 18,
    fontWeight: "bold",
    marginBottom: 2,
  },
  address: {
    fontSize: 10,
    marginBottom: 6,
  },
  divider: {
    borderBottomWidth: 1.5,
    borderBottomColor: "#000",
    borderStyle: "dashed",
    marginVertical: 8,
  },
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    marginBottom: 2,
  },
  bold: {
    fontWeight: "bold",
  },

  tableHeader: {
    flexDirection: "row",
    borderBottomWidth: 1.5,
    borderBottomColor: "#000",
    borderStyle: "dashed",
    marginBottom: 4,
    paddingTop: 4,
    paddingBottom: 4,
    alignItems: "center",
  },

  // Description: flexible column, can shrink and wrap inside its box
  colDesc: {
    flexBasis: 0,
    flexGrow: 1,
    flexShrink: 1,
    minWidth: 0, // critical so it can shrink inside the row
    // reserve horizontal room so the description cannot flow under Qty/Amount
    paddingRight: 92, // 28 (qty) + 56 (amount) + gap
    // break very long continuous strings so they wrap inside the column
    wordBreak: "break-all",
  },

  // Fixed widths for Qty and Amount so Description cannot overlap them
  colQty: {
    width: 28,
    textAlign: "center",
  },

  colAmount: {
    width: 56,
    textAlign: "right",
  },

  tableRow: {
    flexDirection: "row",
    alignItems: "flex-start",
    marginBottom: 6,
    // allow lines inside the description column to wrap
    // (removed flexWrap: "nowrap" so desc can wrap)
  },

  totalRow: {
    flexDirection: "row",
    justifyContent: "flex-end",
    marginTop: 8,
    fontWeight: "bold",
    fontSize: 13,
  },
  footer: {
    textAlign: "center",
    marginTop: 8,
    fontSize: 10,
  },
  solution: {
    textAlign: "center",
    marginTop: 8,
    fontSize: 10,
    fontWeight: "bold",
  },
});