import React from "react";
import { useCart } from "@/contexts/cart-context";

interface Customer {
  id: string;
  name: string;
  points: number;
}

interface ReceiptProps {
  selectedCustomer: Customer | null;
  cartTotal: number;
}

export default function Receipt({ selectedCustomer, cartTotal }: ReceiptProps) {
  const { cart } = useCart();

  async function downloadPdf() {
    const API_BASE =
      (process.env.NEXT_PUBLIC_backend_api_url &&
        process.env.NEXT_PUBLIC_backend_api_url.replace(/\/$/, "")) ||
      "http://localhost:5000/api";

    // build items payload from cart
    const items = (cart || []).map((ci) => {
      const price = Number(ci.product?.price || 0);
      const qty = Number(ci.quantity || 0);
      const desc =
        (ci.product && (ci.product.name || ci.product.title)) ||
        ci.product?.barcode ||
        "Item";
      return {
        desc,
        qty,
        amount: Number((price * qty).toFixed(2)),
      };
    });

    const data = {
      customer: selectedCustomer || { name: "N/A" },
      cartTotal: Number(cartTotal || 0),
      amount: Number(cartTotal || 0), // if paid amount differs, pass real paid amount here
      change: 0, // replace with actual change if available
      items,
    };

    try {
      const res = await fetch(`${API_BASE}/receipt`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
      });

      if (!res.ok) {
        const text = await res.text().catch(() => "");
        console.error("PDF request failed:", res.status, res.statusText, text);
        return;
      }

      const blob = await res.blob();
      const url = URL.createObjectURL(blob);
      window.open(url, "_blank");
      setTimeout(() => URL.revokeObjectURL(url), 20000);
    } catch (err) {
      console.error("PDF download error:", err);
    }
  }

  return (
    <div className="flex flex-col items-center justify-center flex-1">
      <h2 className="text-3xl font-bold mb-4">Transaction Successful!</h2>
      <p className="mb-8">Thank you for your purchase.</p>

      <button
        onClick={downloadPdf}
        className="px-4 py-2 bg-indigo-600 text-white rounded"
      >
        Download Receipt (PDF)
      </button>
    </div>
  );
}